/* NAVBAR */
.navbar {
    background-color: white;
    padding: 10px;
    box-shadow: 0px 3px 7px 1px rgba(0, 0, 0, 0.07),
    0px -3px 7px 1px rgba(0, 0, 0, 0.07);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav-brand {
    font-size: 40px;
    font-weight: 450;
    text-decoration: none;
    padding-left: 20px;
    color: initial;
}
.nav-brand:hover {
    text-decoration: none;
}
.nav-brand:active {
    color: var(--primary-1);
}

.navbar ul {
    list-style-type: none;
    overflow: hidden;
    padding-right: 20px;
    text-align: center;
}

.navbar li {
    display: inline-block;
}

.nav-item a {
    text-decoration: none;
    font-weight: normal;
    color: var(--primary-1);
    display: inline-block;
    text-align: center;
    padding: 13px 20px;
    min-width: 75px;
}

.nav-item:hover a {
    text-decoration: none;
    color: var(--primary-2);
}

.nav-item a.active {
    font-weight: bold;
}

.nav-cart {
    width: 65px;
    text-align: center;
    padding: 10px 25px;
}
